input {
    margin-top: 15px !important;
  }
  lottie-player {
    /* border-radius: 10%; */
    /* border: 1px solid #1877f2; */
    overflow: hidden;
  }
  
  img {
    height: 200px;
    width: 200px;
    margin-top: 10px;
    border-radius: 10%;
  }
  img:hover {
    transform: scale(1.07);
    transition: 1s ease-out;
  }
  
  .auth-form {
    padding: 20px;
    border: 4px solid blue;
    background-color: rgb(158, 245, 77);
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
      rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  }
  
  .z1 {
    z-index: 1;
  }
  
  .auth-btn {
    background: blue;
    border: 1px solid #1877f2;
    color: #fff;
    width: 50%;
    margin: 1rem auto;
    text-align: center;
    font-size: 1rem;
    padding: 0.5rem;
    /* margin: 1rem 0; */
    border-radius: 8px;
    outline: none;
    cursor: pointer;
  }
  
  .form-control {
    border: 2px solid blue;
    background-color: rgb(158, 245, 77);
  }
  
  /* RegisterPage */
  
  .register-parent {
    min-height: 81vh;
    background-color: aliceblue;
    /* align-items: center; */
  }
  
  /* .register-parent .row {
      height: 81vh;
      align-items: center;
  } */
  
  .register-top {
    height: 50%;
    position: absolute;
    left: 0;
    right: 0;
    /* top: 0; */
    bottom: 0;
    background-color: #003f5c;
  }
  
  /* LoginPage */
  
  .login-parent {
    min-height: 81vh;
    background-color: aliceblue;
    /* align-items: center; */
  }
  
  /* .login-parent .row {
      height: 81vh;
      align-items: center;
  } */
  
  .login-bottom {
    height: 50%;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #003f5c;
  }
  
  /* Contact Page */
  
  .contact-parent {
    min-height: 81vh;
    /* background-color: aliceblue; */
  }
  
  .form {
    background: #fff;
    border: 4px solid blue;
    box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
    border-radius: 8px;
    padding: 1rem;
  }
  
  .form input,
  textarea {
    border-radius: 8px;
    border: 2px solid blue;
    outline: none;
    color: #1d2129;
    /* margin: 1rem 0; */
    padding: 0.5rem 0.75rem;
    width: 70%;
    font-size: 1rem;
  }
  